img {
    height: 50px;
    width: 50px;
}

* {
    margin: 0;
    padding: 0;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

html {
    background-color: #121212;
}
#root {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}
button {
    background: none;
    color: inherit;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
}
ul {
    list-style-type: none;
}
input {
    background: none;
    color: inherit;
    border: none;
    padding: 0;
    font: inherit;
    outline: inherit;
    color: white;
    display: block;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}

.header {
    color: white;
}
.footer {
    color: white;
}

.teams-container {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}
.team-button {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #2e2e2e;
    padding: 2rem;
    margin: 1rem;
    border-radius: 5%;
}
.selected {
    background-color: #3700B3;
}
.team-money {
    display: flex;
    flex-wrap: wrap;
    max-width: 250px;
}
.team-name {
    display: inline;
    font-size: 2.5rem;
    margin: 10px 0 10px 0;
    width: 350px;
}
.team-inv {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    max-width: 250px;
}

.cards-container {
    display: flex;
    flex-wrap: wrap;
}
.item-container {
    padding: 1rem;
    margin: 1rem;
    background-color: #585858;
    border-radius: 5%;
    color: white;
}
.itm-details {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.item-money {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    max-width: 250px;
    justify-content: center;
    margin: 10px 0 10px 0;
}
.itm-label {
    font-size: 2rem;
}
.itm-desc {
    font-size: 1rem;
    margin: 10px 0 10px 0;
}
.itm-qty , .itm-num {
    font-size: 0.8rem;
    color: #e2e2e2;
}
.itm-dir {
    font-size: 0.8rem;
    color: #cecece;
}